<template>
	<el-form :model="ruleForm" :rules="rules" ref="ruleForm">
		<el-form-item prop="old_pwd">
			<el-input type="password" v-model="ruleForm.old_pwd" auto-complete="off" placeholder="旧密码"></el-input>
		</el-form-item>
		<el-form-item prop="new_pwd">
			<el-input type="password" v-model="ruleForm.new_pwd" auto-complete="off" placeholder="新密码"></el-input>
		</el-form-item>
		<el-form-item prop="repeatPass">
			<el-input type="password" v-model="ruleForm.repeatPass" auto-complete="off" placeholder="确认新密码"></el-input>
		</el-form-item>
		<el-form-item class="form-submit">
		  <el-button type="primary" @click="submitForm" :loading="logining">保存</el-button>
		  <el-button @click="$router.go(-1)">取消</el-button>
		</el-form-item>
	</el-form>
	
</template>

<script>
import { price, duration, score, type, film_url } from '@/assets/utils/validate';
import { editPassword} from '@/api/api';
export default {
	name: '',
	data () {
		var validatePassOld = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'));
			} else {
				
				callback();
			}
		};
		var validatePass = (rule, value, callback) => {
			
			let s = 0;
			if( value.length <6 ){
				s = 0;
				callback(new Error('密码长度不能小于6位！'));
			};
			if( /[a-zA-Z]/.test(value) ){
				s++;
			};
			if( /[0-9]/.test(value) ){
				s++;
			};
			if( /[^0-9a-zA-Z]/.test(value) ){
				s++;
			};
			if(s == 1){
				callback(new Error('密码必须含有数字和字母！'));
			}else{
				callback();
			}
			
		};
		var validatePass2 = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请再次输入新密码'));
			} else if (value !== this.ruleForm.new_pwd) {
				callback(new Error('两次输入密码不一致!'));
			}  else {
				callback();
			}
		};
		return {
			msg: '404',
			logining: false,
			ruleForm: {
				uid: '',
				old_pwd: '',
				new_pwd: '',
				repeatPass: ''
			},
			rules: {
				old_pwd: [
					{ validator: validatePassOld, trigger: 'blur' },
				],
				new_pwd: [
					{ validator: validatePass, trigger: 'blur' },
				],
				repeatPass: [
					{ validator: validatePass2, trigger: 'blur' },
				]
			}

		}
	},
  	methods: {
		submitForm(ev) {
			this.$refs.ruleForm.validate((valid) => {
				if (valid) {
					this.logining = true;
					this.ruleForm.uid = JSON.parse(localStorage.user).admin_userid;
					editPassword(this.ruleForm).then(res => {
						this.logining = false;
						if(res.data.status == '200'){
							this.$message.success(res.data.msg);
						}else{
							this.$message.error(res.data.msg);
						}
					});
				} else {
				  console.log('error submit!!');
				  return false;
				}
			});
		},
		selsChange: function(sels) {
			this.sels = sels;
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped >
	
</style>



